<template>
  <div class="Install">
    <div
      class="InstallBox"
      :class="item.CaseClosed == '1' ? 'closed' : ''"
      v-for="(item, index) in InstallList"
      :key="index"
    >
      <div class="InstallBoxTop" @click="toInstallBasics(item.IncidentNo)">
        <img
          v-if="item.CustPictureUrl"
          class="InstallBoxTopHeadPortrait"
          draggable="false"
          :src="item.CustPictureUrl"
          alt=""
        />
        <img
          v-if="!item.CustPictureUrl"
          class="InstallBoxTopHeadPortrait"
          draggable="false"
          src="@/assets/image/InstallHeadPortrait.png"
          alt=""
        />
        <p>
          <em>{{ item.CustName }}</em>
          <img
            v-if="item.statusImgUrl"
            draggable="false"
            src="@/assets/image/InstallCompany.png"
            alt=""
          />
          <span>淨水器機型：{{ item.ModelName }}</span>
        </p>
      </div>
      <div class="InstallBoxBottom">
        <h2>單號：{{ item.IncidentNo }}</h2>
        <h3>到期日：{{ item.IncidentDate.split("T")[0] }}</h3>
      </div>
    </div>
    <!-- 空组件 -->
    <noContent v-if="dataFormListStatus" />
    <!-- 加载中组件 -->
    <Loading :show="loadingShow" />
  </div>
</template>

<script>
import noContent from "@/components/noContent";
import Loading from "@/components/Loading";
import { POST_GetIncident } from "@/utils/api";
export default {
  name: "Install",
  components: { noContent, Loading },
  data() {
    return {
      loadingShow: false,
      dataFormListStatus: false,
      InstallList: [],
      closed: 1,
    };
  },
  created() {
    this.GetIncident();
  },
  methods: {
    GetIncident() {
      this.loadingShow = true;
      var UofIds = {
        DistributionUofId: sessionStorage.getItem("DistributionUofId"),
      };
      POST_GetIncident(UofIds).then((response) => {
        this.loadingShow = false;
        if (response.State) {
          if (this.$route.query.CustPhone) {
            for (var d = 0; d < response.Data.length; d++) {
              // var a1 = response.Data[d].Mobile.split("-")[0];
              // var a2 = response.Data[d].Mobile.split("-")[1];
              if (response.Data[d].Mobile == this.$route.query.CustPhone) {
                this.InstallList.push(response.Data[d]);
              }
            }
            // 判断列表没有数据是显示空组件
            if (this.InstallList.length == 0) {
              this.dataFormListStatus = true;
            } else {
              this.dataFormListStatus = false;
            }
          } else {
            this.InstallList = response.Data;
            // 判断列表没有数据是显示空组件
            if (this.InstallList.length == 0) {
              this.dataFormListStatus = true;
            } else {
              this.dataFormListStatus = false;
            }
          }
        } else {
          this.dataFormListStatus = true;
        }
      });
    },
    toInstallBasics(CustId) {
      this.$router.push("/InstallReplacement/InstallBasics?CustId=" + CustId);
    },
  },
};
</script>

<style lang="scss" scoped>
.Install {
  padding-top: 24px;
  .InstallBox {
    padding: 16px 5.37%;
    height: 70px;
    background: #ffffff;
    border-radius: 8px;
    margin-bottom: 10px;
    .InstallBoxTop {
      display: flex;
      align-items: center;
      margin-bottom: 14px;
      .InstallBoxTopHeadPortrait {
        width: 40px;
        height: 40px;
        border-radius: 50%;
      }
      p {
        margin-left: 2%;
        em {
          font-size: 16px;
          line-height: 22px;
          color: #3d3d3d;
        }
        img {
          margin-left: 2%;
          height: 17px;
          width: 35%;
        }
        span {
          display: block;
          color: #bdbdbd;
          font-size: 12px;
          line-height: 17px;
        }
      }
    }
    .InstallBoxBottom {
      width: 100%;
      display: inline-block;
      justify-content: space-between;
      h2,
      h3 {
        color: #3d3d3d;
        font-size: 12px;
        display: inline-block;
        line-height: 17px;
      }
      h3 {
        margin-left: 5%;
      }
    }
  }
  .InstallBox.closed {
    background: #f6f6f7;
    .InstallBoxTop {
      .InstallBoxTopHeadPortrait {
        -webkit-filter: grayscale(100%); /* Chrome, Safari, Opera */
        filter: grayscale(100%);
      }
      p {
        em {
          color: #bdbdbd;
        }
      }
    }
    .InstallBoxBottom {
      h2,
      h3 {
        color: #bdbdbd;
      }
    }
  }
}
</style>
